<template>
  <div class="login-container">
    <div class="login-wrapper">
      <div class="login-box">
        <div class="login">
          <el-input v-model="userConfig.username" placeholder="输入账号"></el-input>
          <el-input v-model="userConfig.password" placeholder="输入密码" show-password></el-input>
          <el-button type="primary" style="margin-top:10px;" @click="login">登录</el-button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */ 
import userApi from '@/Api/user-operation'
export default {
  data() {
    return {
     userConfig:{
        username: "",
        password: ""
     }
    }
  },
  methods: {
    login() {
      let formData = new FormData()
      formData.append('username', this.userConfig.username)
      formData.append('password', this.userConfig.password)
      userApi.login(formData).then(response => {
        if(response.data.role === '普通用户'){
          this.$message.error('用户权限无法登录该系统')
          return
        }
        this.$message.success('登录成功')
        window.sessionStorage.setItem("islogined", 1)
        window.sessionStorage.setItem('session',response.data.session)
        this.$router.push({
          name: "main",
          query: {
            account: this.userConfig.username
          }
        });
      }).catch(error => {
        this.$message.error('登录失败')
      })
    }
  }
};
</script>

<style lang="scss" scope>
.login-container {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #545c64;
  .login-wrapper {
    width: 100%;
    position: relative;
    height: 300px;
    .login-box {
      width: 500px;
      height: 350px;
      background: #fff;
      // margin: auto;
      position: absolute;
      top: 40%;
      left: 35%;
      border-radius: 10px;
      .login {
        margin: 70px;
        .el-input {
          margin-top: 20px;
        }
      }
    }
  }
}
</style>



