<template>
<el-main class="main-bar">
                <div class="main-header">
                   <el-breadcrumb separator="/">
                    <el-breadcrumb-item :to="{ path: '/' }">用户列表</el-breadcrumb-item>
                    </el-breadcrumb>
                </div>
                <div class="main-action">
                   <el-button icon="el-icon-plus" size="small" style="border:none" @click="handleAdd">
                        <span>添加</span>
                   </el-button>
                </div>
                <div class="main-content">
                    <el-table :data="tableData" style="width:100%">
                        <el-table-column type="index" width="75px" label="序号">
                        </el-table-column>
                        <el-table-column v-for="(title,index) in titles" :key="index" :label="title.label"
                        :property="title.prop"
                        >
                        </el-table-column>
                        <el-table-column label="操作" width="200px">
                            <template slot-scope="scope">
                                <el-button size="mini" @click="handleEdit(scope.row,scope.$index)">
                                    编辑
                                </el-button>
                                <el-button size="mini" @click="handleDelete(scope.row,scope.$index)" type="danger">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </div>
            </el-main>
</template>
<script>
/* eslint-disable */
import userApi from '@/Api/user-operation'
export default {
    data(){
        return{
            isCollapse:false,
            titles:[
                {label: "账户名",prop: "username"},
                {label: "电子邮件",prop: "email"},
                {label: "性别",prop: "sex"},
                {label: "角色",prop: "role"}
            ],
            tableData:[]
        }
    },
    created(){
        this.getUserList()
    },
    methods:{
        getUserList(){
            userApi.getUserList().then(response => {
               // console.log(response)
                this.tableData = response.data
            }).catch({

            })
        },
        handleCollapse(){
            this.isCollapse = !this.isCollapse
        },
        handleEdit( row, index ){
            this.$router.push({
                path:'/main/useredit',
                query:{
                    username:row.username,
                    email:row.email,
                    role: row.role
                }
            })
        },
        handleDelete( row , index ){
            const h = this.$createElement
            this.$msgbox({
                title:'删除提示',
                message: h('p',null,[
                    h('span',null,'确定删除用户' + row.username + '?')
                ]),
                showCancelButton:true,
                confirmButtonText:'确定',
                cancelButtonText:'取消',
                callback: action => {
                    if(action === 'confirm'){
                        let form = new FormData()
                        form.append('username',row.username)
                        userApi.userDelete(form).then(response => {
                            this.$message.success('删除成功')
                            this.tableData.splice(index,1)
                        }).catch(error => {
                            this.$message.error('删除失败')
                        })
                    }
                }
            })
        },
        handleAdd(){
            this.$router.push({
                path:'/main/useredit',
            })
        }
    }
}
</script>

<style lang="scss" scope>
.main-bar{
        padding: 0;
        .main-header{
            border-bottom: 1px solid #ccc;
            // box-shadow: 0 0 1px #ccc;
            height: 38px;
            line-height: 38px;
            padding-left: 5px;
            .el-breadcrumb{
                line-height: 38px;
            }
        }
        .main-action{
            margin-top: 5px;
            height: 48px;
            line-height: 30px;
            padding-left: 5px;
        }
        .main-content{

        }
    }
</style>



