import Main from '@/components/Main'
import Login from '@/components/Login'
import UserTable from '@/pages/user-table'
import Config from '@/pages/save-config'
import UserEdit from '@/pages/user-edit'
const routes = [
  {
    path: '/',
    redirect: '/main/usertable'
  },
  {
    path: '/main',
    name: 'main',
    component: Main,
    redirect: '/main/usertable',
    children: [
      {
        name: 'userTable',
        path: '/main/usertable',
        component: UserTable
      }, {
        name: 'userConfig',
        path: '/main/config',
        component: Config
      }, {
        name: 'userEdit',
        path: '/main/useredit',
        component: UserEdit
      }
    ]
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  }
]
export default routes



