<template>
<div class="page">
  <div class="head">
     <el-breadcrumb separator="/">
         <el-breadcrumb-item :to="{ path: '/' }"><el-button style="border:none"><i class="el-icon-back"></i>用户列表</el-button></el-breadcrumb-item>
      </el-breadcrumb>
  </div>
  <div class="page">
      <div class="config-container">
        <div class="config-box">
            <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">用户名</div>
                <span v-if="isEdit">{{username}}</span>
                <el-input placeholder="请输入用户名" v-model="username" v-else></el-input>
            </div>
             <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">电子邮件</div>
                <span v-if="isEdit">{{email}}</span>
                <el-input placeholder="请输入邮箱" v-model="email" v-else></el-input>
            </div>
            <div class="config-item" v-if="!isEdit">
                <div style="color:gray;margin-bottom:5px;">密码</div>
                <el-input placeholder="请输入密码" type="password" v-model="password"></el-input>
            </div>
            <div class="config-item" v-if="!isEdit">
                <div style="color:gray;margin-bottom:5px;">性别</div>
                <el-select v-model="sex" placeholder="">
                  <el-option v-for="(sex,index) in sexs" :key="index" :label="sex.sexName"
                  :value="sex.sexId"></el-option>
                </el-select>
            </div>
             <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">角色</div>
                <el-select v-model="role" placeholder="">
                  <el-option v-for="(role,index) in roles" :key="index" :label="role.roleName"
                  :value="role.roleId"></el-option>
                </el-select>
            </div>
            <div class="config-item">
                <el-button type="danger" @click="handleEdit">提交设置</el-button>
            </div>
        </div>
    </div>
  </div>
</div>
</template>

<script>
import userApi from '@/Api/user-operation'
export default {
  data () {
    return {
      roles: [
        { roleName: '管理员', roleId: 1 },
        { roleName: '普通用户', roleId: 2 }
      ],
      sexs: [
        { sexName: '男', sexId: 1 },
        { sexName: '女', sexId: 2 }
      ],
      role: '',
      isEdit: false,
      username: '',
      email: '',
      password: '',
      sex: ''
    }
  },
  created () {
    let username = this.$route.query.username
    if (username) {
      this.isEdit = true
      this.username = username
      this.email = this.$route.query.email
      this.role = this.$route.query.role
    }
  },
  methods: {
    handleEdit () {
      let formData = new FormData()
      if (this.isEdit) {
        formData.append('username', this.username)
        userApi.userEdit(formData).then(response => {
          this.$message.success('修改成功')
        }).catch(error => {
          this.$message.error('修改成功' || error.msg)
        })
      } else {
        formData.append('username', this.username)
        formData.append('email', this.email)
        formData.append('password', this.password)
        formData.append('sex', this.sex)
        userApi.userAdd().then(response => {
          this.$message.success('添加成功')
        }).catch(error => {
          this.$message.error('添加失败' || error.msg)
        })
      }
    }
  }
}
</script>

<style lang="scss" scope>
.page{
  width: 100%;
  height: 100%;
  margin: 10px auto;
  .head{
    width: 90%;
    margin-left: 2%;
  }
  .page{
    width: 30%;
    .config-container{
      margin: 50px auto;
    .config-box{
        display: flex;
        flex-direction: column;
        .config-item{
            margin:15px 0;
        }
    }
  }
  }
}
</style>



