<template>
    <el-container class="container">
        <el-header class="top-bar">
                <div class="title">后台管理系统</div>
                <div class="person-item">
                    <div class="items">
                        <img src="" alt="">
                        <span>{{account}}</span>
                    </div>
                </div>
        </el-header>
        <el-container>
            <el-aside width="200px" class="aside-bar">
                <el-row class="tac">
                    <el-menu default-active="1" class="el-menu-vertical-demo"
                    @open="handleOpen" @close="handleClose"
                    background-color="#545c64"
                    text-color="#fff"
                    active-text-color="#ffd04b"
                    :collapse="isCollapse"
                    >
                        <el-menu-item @click="handleCollapse">
                            <i class="el-icon-s-unfold"></i>
                        </el-menu-item>
                        <el-menu-item index="1" @click="handleList">
                        <i class="el-icon-menu"></i>
                        <span slot="title" >用户列表</span>
                        </el-menu-item>
                        <el-menu-item index="2" @click="handleConfig">
                        <i class="el-icon-s-tools"></i>
                        <span slot="title" >用户配置修改</span>
                        </el-menu-item>
                        <el-menu-item index="3" @click="handleQuit">
                        <i class="el-icon-s-custom"></i>
                        <span slot="title" >退出</span>
                        </el-menu-item>
                    </el-menu>
                </el-row>
            </el-aside>
            <el-container>
                <router-view></router-view>
            </el-container>
        </el-container>
    </el-container>
</template>
<script>
/* eslint-disable */
import userApi from '@/Api/user-operation'
export default {
    data(){
        return{
            isCollapse:false,
            account:''
        }
    },
    created(){
        this.account = this.$route.query.account
    },
    methods:{
        handleCollapse(){
            this.isCollapse = !this.isCollapse
        },
        handleOpen(){
            
        },
        handleClose(){
            
        },
        handleQuit(){
            let sessionId = window.sessionStorage.getItem('session')
            let formData = new FormData()
            formData.append('session',sessionId)
            userApi.userLoginOut(formData).then(response => {
                this.$message.success('登出成功')
                window.sessionStorage.clear()
                this.$router.push({
                    path:'/login',
                })
            }).catch(error => {
                this.$message.error('登出失败')
            })
        },
        handleConfig(){
            this.$router.push({
                path:'/main/config',
            })
        },
        handleList(){
            this.$router.push({
                path:'/main/usertable'
            })
        }   
    }
}
</script>
<style lang="scss" scope>
.container{
    .top-bar{
        background:rgba($color: #000000, $alpha: 0.8);
        display: flex;
        justify-content: space-between;
        color: white;
        .title{
            font-size: 18px;
            line-height: 60px;
        }
        .person-item{
            // margin: 15px auto;
            .items{
                margin:15px auto;
                img{
                width: 30px;
                height: 30px;
                }
                span{

                }
            }
        }
    }
    .aside-bar{
        .el-menu{
            height: 800px;
        }
    }
}
</style>



