import httpApi from '@/Api/httpInstance'

export default {
  getUserList: function (params) {
    return httpApi.get('/manage/userList', params)
  },
  login: function (params) {
    return httpApi.post('/auth/manage/login', params)
  },
  userDelete: function (params) {
    return httpApi.post('manage/userDelete', params)
  },
  userEdit: function (params) {
    return httpApi.post('/manage/userEdit', params)
  },
  userAdd: function (params) {
    return httpApi.post('/manage/userAdd'.params)
  },
  userLoginOut: function (params) {
    return httpApi.post('/auth/logout', params)
  }
}



