<template>
    <div class="config-container">
        <div class="config-box">
            <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">存储池配置</div>
                <el-select v-model="position" placeholder="">
                  <el-option v-for="(position,index) in positions" :key="index" :label="position.positionName"
                  :value="position.positionName"></el-option>
                </el-select>
            </div>
             <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">图像识别接口配置</div>
                <el-select v-model="imgUrl" placeholder="">
                  <el-option v-for="(imgUrl,index) in imgUrls" :key="index" :label="imgUrl.imgUrlName"
                  :value="imgUrl.imgUrlId"></el-option>
                </el-select>
            </div>
             <div class="config-item">
                <div style="color:gray;margin-bottom:5px;">文字甄别接口配置</div>
                <el-select v-model="textUrl" placeholder="">
                  <el-option v-for="(textUrl,index) in textUrls" :key="index" :label="textUrl.textUrlName"
                  :value="textUrl.textUrlId"></el-option>
                </el-select>
            </div>
            <div class="config-item">
                <el-button type="danger" @click="handleCofig">提交设置</el-button>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */ 
import userApi from '@/Api/user-config'
export default {
    data(){
        return{
            positions:[
                { positionName: '本地', positionId: 1},
                { positionName: 'CDN服务器', positionId: 2},
            ],
            position:'',
            imgUrls:[
                { imgUrlName: '图片服务器1', imgUrlId: 1},
                {imgUrlName: '图片服务器2', imgUrlId: 2}
            ],
            textUrls:[
                { textUrlName: '文字服务器1', textUrlId: 1},
                { textUrlName: '文字服务器2', textUrlId: 2}
            ],
            imgUrl: '',
            textUrl: ''
        }
    },
    methods:{
        handleCofig(){
            let formData = new FormData()
            formData.append('position',this.position)
            formData.append('imgposition',this.imgUrl)
            formData.append('textposition',this.textUrl)
            userApi.postUserConfig(formData).then(response => {
                this.$message.success('参数设置成功')
                this.$router.push({
                    path:'/',
                })
            }).catch(error => {
                this.$message.error('参数设置失败')
            })
        }
    }
}
</script>

<style lang="scss" scope>
.config-container{
    margin: 50px auto;
    .config-box{
        display: flex;
        flex-direction: column;
        .config-item{
            margin:15px 0;
        }
    }
}
</style>



